import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { TextAreaProps } from './TextArea.interface';
import React from 'react';
import { twMerge } from 'tailwind-merge';

export const TextArea: React.FC<TextAreaProps> = ({
  disabled = false,
  label,
  message,
  placeholder,
  readonly = false,
  state = 'default',
  totalCharacters = 500,
  ...props
}) => {
  const inputRef = React.useRef(null);
  const hasError = state === 'error';
  const [query, setQuery] = React.useState('');

  const disabledOrReadonly = disabled || readonly;
  const iconColor = hasError ? 'zep-text-error' : state === 'info' ? 'zep-text-info' : '';

  const color = disabled
    ? 'zep-text-opacity-disabled'
    : readonly
    ? 'zep-text-typography-dark-100'
    : 'zep-text-typography-dark-100 zep-opacity-70';

  const inputClasses = clsx(
    'zep-inline-flex',
    'zep-justify-between',
    'zep-rounded-4',
    'zep-border-solid',
    'zep-pl-1',
    'zep-py-0.75',
    'zep-font-roboto',
    'zep-w-full',
    'zep-outline-none',
    hasError ? 'zep-border-2' : 'zep-border-1',
    'zep-pr-0.75',
    color,
    { 'zep-bg-background-medium': readonly },
    { 'zep-border-neutral-dark-default': !disabled && !hasError },
    { 'zep-border-opacity-disabled': disabled },
    { 'zep-border-error': hasError },
    { 'zep-active:ring': !disabledOrReadonly },
    { 'zep-ring-offset-1': !disabledOrReadonly },
    { 'focus-visible:ring': !disabledOrReadonly },
    { 'zep-ring-focus': !disabledOrReadonly },
    { 'zep-cursor-not-allowed': disabledOrReadonly },
  );
  return (
    <div
      className={twMerge(
        clsx('zep-relative', 'zep-inline-block', 'zep-text-left', 'zep-w-[326px]', {
          'zep-cursor-not-allowed zep-opacity-disabled': disabled,
        }),
        props.className,
      )}
      data-testid="zep-text-area"
      {...props}
    >
      {label && <div className={clsx('zep-block', 'zep-mb-0.5')}>{label}</div>}
      <div className="zep-relative zep-mt-0.5">
        <textarea
          className={inputClasses}
          maxLength={500}
          autoComplete="off"
          placeholder={placeholder}
          disabled={disabled || readonly}
          ref={inputRef}
          data-testid="zep-text-area-input"
          onChange={(event) => setQuery(event.target.value)}
        />
      </div>
      <div
        className={clsx(
          'zep-mt-[4px]',
          'zep-text-typography-dark-100 zep-opacity-70',
          'zep-text-0.875',
          'zep-flex',
          'zep-items-center',
          iconColor,
        )}
      >
        {message && (
          <div className={clsx('zep-flex zep-items-center')}>
            {state !== 'default' && (
              <FunctionalIcon
                name={state === 'error' ? 'warning-triangle-filled' : 'info-circle-filled'}
                role="img"
                size={24}
                className="zep-mr-0.5 zep-shrink-0"
              />
            )}
            <span>{message}</span>
          </div>
        )}
        <div className={clsx('zep-ml-auto')} data-testid="zep-text-area-length-indicator">
          {query.length} / {totalCharacters}
        </div>
      </div>
    </div>
  );
};
